/**
 * Bootstrap Screen Readers Only
 */
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
}

/**
 * Animated transition with ease
 */
@mixin anim($speed: 0.3, $attr: all, $animStyle: ease) {
    $speed_s: $speed + s; //string interpolation as seconds
    //
    -webkit-transition: $attr $speed_s $animStyle;
    -moz-transition: $attr $speed_s $animStyle;
    -o-transition: $attr $speed_s $animStyle;
    transition: $attr $speed_s $animStyle;
}

/**
 * Responsive media queries
 * `@include responsive_media(min-width, $tt_breakpt_600px)`
 */
@mixin responsive_media(
    $attr: min-width,
    $breakpoint: 320px,
    $mediaPrint: false
) {
    @if $mediaPrint {
        @media print and ($attr: $breakpoint) {
            @content;
        }
    } @else {
        @media screen and ($attr: $breakpoint) {
            @content;
        }
    }
}

/**
 * Browsers prefixes by interpolation
 * ex: `@include prefix(filter, grayscale(50%), moz webkit);`
 */
@mixin prefix($property, $value, $broswer_prefixes) {
    @each $broswer_prefix in $broswer_prefixes {
        -#{$broswer_prefix}-#{$property}: $value;
    }
    #{$property}: $value;
}
