/**
 * ADVANCED CUSTOM FIELDS
 * contents generated via plugin.
 * fields are registered 
 */

.acf {
    /**
    * PRODUCT PRICE
    * template: inc/acf.product-price_frCA.html
    * The following ACF `name` values are availables:
    *   * .price
    *   * .pricecents
    */
    &.product-price {
        margin: 0 auto;
        font-family: var(--tt-fontfamily-content);
        font-weight: normal;
        color: var(--tt-color-accent);
        //
        & > .value {
            position: relative;
            margin-top: -0.5rem;
            margin-bottom: -0.6rem;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            background: transparent;
        }
        .price,
        .currency {
            @extend .heading-size-4 !optional;
            margin: 0;
            padding-right: 0.3rem; // prefix
            line-height: 1.138888889;
        }
        .price {
            &.dollars {
                @extend .heading-size-1 !optional;
                margin: 0;
            }
        }
        .txinc {
            display: block;
            text-align: center;
            text-transform: lowercase;
            color: var(--tt-color-secondary);
            &:before {
                content: '(';
            }
            &:after {
                content: ')';
            }
        }
    }

    // Product inventory
    &.inventory {
        margin: 1rem auto -3rem;
        text-align: center;
        font-weight: bold;
        font-style: italic;
        line-height: 1.2;
        color: var(--tt-color-primary);
        font-size: 1.5rem;
        @include responsive_media(min-width, $tt_breakpt_700px) {
            font-size: 1.7rem;
        }
        strong {
            color: var(--tt-color-accent);
        }
    }

    /**
    * PRODUCT SPECIFICATIONS
    * template: inc/acf.product-specifications_frCA.html
    * The following ACF `name` values are availables:
    *   1 .winetype
    *   2 .cepages
    *   3 .vintage
    *   4 .alcohol
    *   5 .sugar
    *   6 .size
    *   7 .weight
    *   8 .height
    *   9 .diameter
    */
    &.product-specs {
        .value {
            // suffixes
            & > span:after,
            & span.value:before,
            & span.value:after {
                padding-right: 0; // prefix
                padding-left: 0.5rem; // suffix
                font-size: 0.76em;
                color: var(--tt-color-secondary);
            }
            &.alcohol {
                span:after {
                    content: '% alc./vol.';
                }
            }
            &.sugar {
                span:after {
                    content: 'g/L.';
                }
            }
            &.size {
                span:after {
                    content: 'ml';
                }
            }
            // dd.bottle ul.collumns
            &.weight {
                &:after {
                    content: 'g';
                }
            }
            &.height,
            &.diameter {
                &:after {
                    content: 'cm';
                }
            }
        }
        .label {
            font-family: var(--tt-fontfamily-base);
            color: var(--tt-color-secondary);
            font-size: 1.4rem;
            @include responsive_media(min-width, $tt_breakpt_700px) {
                font-size: 1.5rem;
            }
        }
        & > .name,
        & > .value {
            line-height: 1.1;
        }
        & > .name {
            text-align: right;
        }
        & > .value > ul {
            margin-bottom: 0;
            margin-left: 0;

            &.inline-values,
            &.columns {
                // list items
                li {
                    display: inline-block;
                    list-style: none;
                }
            }
            &.inline-values {
                margin-top: 0.05rem;

                li {
                    margin: 0 0.6rem 0.05rem 0;
                    line-height: 1.22;

                    &:after {
                        content: '\002c';
                        padding-left: 0.07rem;
                    }
                    &:last-child:after {
                        content: '.';
                    }
                }
            }
            // dd.bottle
            &.columns {
                margin-top: -0.3rem; // vertical aligned with dt.label

                li {
                    margin: 0 1.25rem 0.05rem 0;

                    .label {
                        font-weight: normal;

                        &:after {
                            content: ':';
                        }
                    }
                }
            }
        }
    }
}
