/**
 * BASE
 * revert generic stuff inherited from twentytwenty
 */
.twenty20 {
    // <section> tags
    @at-root section#{&} {
        padding-top: 0;
        padding-bottom: 0;
    }
}

/**
 * LAYOUT
 * theme: twenty20-vignoble
 */
.singular,
.single-post,
article.type-post {
    .entry-header {
        padding-top: 4rem;
    }

    &.has-post-thumbnail {
        .entry-header {
            padding-bottom: 4rem;
        }
        .featured-media {
            img {
                max-width: 300px;
            }
        }

        &.page-template-singular-product {
            .entry-header {
                padding-top: 0;
                &:first-child {
                    padding-bottom: 0;
                }
            }
            .featured-media {
                padding-bottom: 4rem;
            }
        }
    }
}
// header
.header-titles {
    display: flex;
    align-items: center;

    @include responsive_media(max-width, $tt_breakpt_1000px) {
        .site-description {
            margin: 1rem 0 0 1.5rem;
        }
    }
}
// footer
#site-footer {
    .section-inner {
        justify-content: flex-end; // a.to-the-top

        .footer-credits {
            display: none !important;
        }
    }
}

/**
 * SINGULAR PRODUCT CONTENTS
 */
.product-content {
    position: relative;

    #saq-taste-tag {
        margin: 0;
        position: absolute;
        top: 10rem;
        right: 1%;
        @include responsive_media(min-width, $tt_breakpt_700px) {
            top: 16rem;
            right: 20vw;
        }
        @include responsive_media(min-width, $tt_breakpt_1000px) {
            right: 30vw;
        }
        @include responsive_media(min-width, $tt_breakpt_1330px) {
            top: 21rem;
        }
    }

    .entry-content {
        
        // Hide Gutenberg polution `<p></p>`
        p:empty {
            display: none;
        }
    }

    dl.inline-grid {
        display: grid;
        grid-row-gap: 1rem;
        grid-column-gap: 2rem;
        grid-template-columns: [names] 40% [values] auto;

        // reset existing styles that breaks the grid
        dt + dd,
        dd + dt {
            margin-top: 0; // replaced by `grid-column-gap` attribute
        }
        dd {
            margin-left: 0;
            margin-bottom: 0; // replaced by `grid-column-gap` attribute
        }
    }
}
/**
 * custom_taxonomy wine
 */
body.wine-post,
article.type-post {
    .post-meta-wrapper {
        &.post-meta-single-top {
            display: none;
        }
    }
}

/**
 * Home
 */
.front-page {
    h1.entry-title {
        @extend .sr-only !optional;
    }
}
