/**
 * VARIABLES
 * `$tt` = `Twenty Twenty` prefix
 */


// Font-family
$tt_fontFamily_base: "Inter var", -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, sans-serif;
$tt_fontFamily_content: NonBreakingSpaceOverride, "Hoefler Text", Garamond, "Times New Roman", serif;

// Colors from `twentytwenty_get_elements_array()` (functions.php,#723)
$tt_color-accent_pigment: #cd2653; // --accent was #cd2653
$tt_color-accent_txt: #fff;
$tt_color-accent_container: #fff;

$tt_color-primary_pigment: #000;
$tt_color-primary_txt: #fff;
$tt_color-primary_container: #fff;

$tt_color-secondary_pigment: #6d6d6d;
$tt_color-secondary_txt: #fff;
$tt_color-secondary_container: #fff;

$tt_color-borders_pigment: #ccc;
$tt_offwhite_pigment: #dcd7ca; // --subtile-background
// black, white and the inbetween tones
$tt_tone0: #fff;
$tt_tone100: #000;


// Breakpoints (named according to their original values)
$tt_breakpt_479px: 479px;
$tt_breakpt_480px: 480px;
$tt_breakpt_599px: 599px;
$tt_breakpt_600px: 600px;
$tt_breakpt_660px: 660px;
$tt_breakpt_700px: 700px;
$tt_breakpt_782px: 782px;
$tt_breakpt_1000px: 1000px;
$tt_breakpt_1220px: 1220px;
$tt_breakpt_1240px: 1240px;
$tt_breakpt_1280px: 1280px;
$tt_breakpt_1330px: 1330px;

// Utility classes as CSS variables: { property: var(--value); }
:root {
	--tt-color-accent: #{$tt_color-accent_pigment};
	--tt-color-borders: #{$tt_color-borders_pigment};
	--tt-color-primary: #{$tt_color-primary_pigment};
	--tt-color-secondary: #{$tt_color-secondary_pigment};
	--tt-color-text: #{$tt_tone100};
	--tt-fontfamily-base: #{ $tt_fontFamily_base};
	--tt-fontfamily-content: #{ $tt_fontFamily_content};
}