// css
:root {
	// wine background-colors
	--saqtag-wine-fruit-light: #e9c5dd;
	--saqtag-wine-fruit-medium-bodied: #dd9626;
	--saqtag-wine-aromatic-supple: #c83339;
	--saqtag-wine-aromatic-robust: #650a34;
	--saqtag-wine-delicate-light: #ecf2c1;
	--saqtag-wine-fruity-vibrant: #f7e621;
	--saqtag-wine-aromatic-mellow: #b8c0de;
	--saqtag-wine-fruity-sweet: #acd3cc;
	--saqtag-wine-fruity-extra-sweet: #9fd9f5;
}
// sass style
span.saqtag {
	box-sizing: border-box;
	display: flex;
	align-items: center;
	text-align: center;
	font-family: Helvetica, Arial, sans-serif;
	font-size: 13px;
	font-weight: bold;
	box-shadow: 1px 2px 5px rgba(black, 0.3);
	transform: rotate(5deg);
	cursor: help;
	// text
	& > em {
		text-transform: uppercase;
		font-style: normal;
	}
	// shapes
	&.wine {
		width: 100px;
		height: 100px;
		padding: 1px;
		border: 4px solid #a89966;
		border-radius: 50%;
		//
		&.fruit-light {
			color: white;
			background-color: var(--saqtag-wine-fruit-light);
		}
		&.fruit-medium-bodied {
			color: white;
			background-color: var(--saqtag-wine-fruit-medium-bodied);
		}
		&.aromatic-supple {
			color: white;
			background-color: var(--saqtag-wine-aromatic-supple);
		}
		&.aromatic-robust {
			color: white;
			background-color: var(--saqtag-wine-aromatic-robust);
		}
		&.delicate-light {
			color: rgba(black, 0.7);
			background-color: var(--saqtag-wine-delicate-light);
		}
		&.fruity-vibrant {
			color: rgba(black, 0.7);
			background-color: var(--saqtag-wine-fruity-vibrant);
		}
		&.aromatic-mellow {
			color: rgba(black, 0.7);
			background-color: var(--saqtag-wine-aromatic-mellow);
		}
		&.fruity-sweet {
			color: rgba(black, 0.7);
			background-color: var(--saqtag-wine-fruity-sweet);
		}
		&.fruity-extra-sweet {
			color: rgba(black, 0.8);
			background-color: var(--saqtag-wine-fruity-extra-sweet);
		}
	}
}